r:#ede1d9;color:#445;font-family:Arial;font-variant:no-common-ligatures;line-height:1.6}h1,h2,h3,h4,h5,h6{color:#4265cb;font-family:Cooper;line-height:1.2}abbr[data-original-title],abbr[title]{cursor:help}dl,ol,ul{list-style-type:none;margin:0;padding:0}pre{margin:0}img{vertical-align:middle}[role=button]{cursor:pointer}[role=button],a,area,button,input,label,select,summary,textarea{touch-action:manipulation}th{text-align:left}label{display:inline-block;margin:0}button,input,select,textarea{border-radius:0;line-height:inherit;margin:0}textarea{resize:vertical}fieldset{margin:0;min-width:0}fieldset,legend{border:0;padding:0}legend{display:block;font-size:1.5rem;line-height:inherit;margin-bottom:.5rem;width:100%}input[type=search]{-webkit-appearance:none;box-sizing:inherit}.gallery-title{font-size:1.728rem;margin-bottom:.75em;margin-left:.75em}.gallery-wrapper{margin:0 -.6944444444rem 3em}.gallery-wrapper:after,.gallery-wrapper:before{background:linear-gradient(90deg,#fff,hsla(0,0%,100%,0));content:\"\";display:block;height:100%;left:-1px;pointer-events:none;position:absolute;top:0;width:1rem;z-index:99}@media screen and (orientation:landscape) and (min-width:800px){.gallery-wrapper:after,.gallery-wrapper:before{width:1.44rem}}.gallery-wrapper:after{background:linear-gradient(270deg,#fff,hsla(0,0%,100%,0));left:auto;right:-1px}.gallery-scroller{cursor:grab;display:flex;flex-direction:row;overflow-x:scroll;overflow-y:hidden;position:relative}.gallery-scroller.grabbing{cursor:grabbing}.gallery-scroller::-webkit-scrollbar{height:0}.gallery-item{background-clip:content-box;border:.6944444444rem solid transparent;flex:0 0 75%;overflow:hidden;padding-top:75%;position:relative;-webkit-user-select:none;-moz-user-select:none;user-select:none}@media screen and (orientation:landscape) and (min-width:800px){.gallery-item{border:1rem solid transparent;flex:0 0 30%;padding-top:27%}}.gallery-item:hover img{opacity:.75}.gallery-item img{bottom:0;height:100%;left:0;max-height:none;max-width:none;-o-object-fit:cover;object-fit:cover;position:absolute;right:0;top:0;transition:opacity .5s ease-in-out;width:100%}.gallery-item a{cursor:pointer;pointer-events:auto}.gallery-item.add{background-clip:padding-box;background-color:#f0f6fc;padding:1rem;text-align:center}.gallery-item.add,.lightbox{align-items:center;display:flex;flex-direction:column;justify-content:center}.lightbox{background-color:#fff;bottom:0;max-width:900px;position:fixed;right:0;top:0;width:55vw;z-index:999999999}@media screen and (orientation:portrait){.lightbox{width:100%}}.lightbox-image{align-items:center;display:flex;flex:1 1 auto;justify-content:center;overflow:hidden}.lightbox-image img{display:block;margin:auto;width:100%}.lightbox-text{display:flex;flex:1 1 auto;flex-direction:column;padding:1rem 0 0;width:100%}.lightbox-text-inner{flex:0 1 auto;margin-top:auto;overflow:auto;padding:0 1.44rem}@media screen and (orientation:portrait){.lightbox-text-inner{padding:0 1rem}}.lightbox-text-inner p{margin-bottom:.4822530864rem}.lightbox-text-inner p:first-child,.lightbox-text-inner p:last-child{margin-bottom:1.2rem}.lightbox-navigation{border-top:1px solid #dfdfe5;display:flex;gap:12px;justify-content:space-between;padding:.6944444444rem 1rem}@media screen and (orientation:portrait){.lightbox-navigation{justify-content:flex-start;padding:.6944444444rem 1rem}}.lightbox-navigation .close{margin-left:auto}", "",{"version":3,"sources":["webpack://./src/assets/styles/_fonts.scss","webpack://./src/assets/scripts/components/Gallery.vue","webpack://./src/assets/styles/_transitions.scss","webpack://./src/assets/styles/base.scss","webpack://./src/assets/styles/_variables.scss"],"names":[],"mappings":"AAAA,WAMI,kBAAA,CALA,kBAAA,CAIA,iBAAA,CADA,eAAA,CAFA,mGCIJ,CCNA,sCACE,qBDQF,CCNA,2BACE,SDSF,CCNA,wCACE,yBAAA,CACA,kCDSF,CCNA,oBAEE,oBAAA,CADA,mCDUF,CCNA,6BACE,0BAAA,CACA,cDSF,CCNA,6BACE,uBAAA,CACA,cDSF,CCLA,wCACE,wBDQF,CCLA,oBACE,sBDQF,CCDA,6BACE,0BDQF,CCHA,+BACE,mCDMF,CCJE,mDACE,6BDMJ,CCFA,+BACE