ght-gray: #f8f8f8;\n$medium-gray: #dfdfe5;\n$new-gray: #afafbf;\n$gray: #85898E;\n$dark-gray: #444455;\n\n$black: #222;\n\n$brand-light-blue: #4265cb;\n$brand-blue: #21378b;\n$brand-light-pink: #f4A8FF;\n$brand-peach: #ede1d9;\n$brand-pink: #dd5cb3;\n\n$blue: #aecdeb;\n$light-blue: #f0f6fc;\n$red: #df7839;\n$orange: #f4d582;\n$cream: #fef6dd;\n\n$text-color: $dark-gray;\n$bg-color: $brand-peach;\n\n$base-timing: ease;\n$base-duration: 500ms;\n\n$base-radius: 0.25rem;\n\n$box-shadow: 0 0.75em 1.2em -0.125em rgba($black, 0.5), 0 0px 0 1px rgba($black, 0.02);"],"sourceRoot":""}]);
