try--enter{background-color:#f4a8ff;border-radius:9999px;color:#21378b;display:block;opacity:0;padding:.25em .75em;position:absolute;right:-.75em;text-align:center;top:0;transform:translateY(.45em);transition:opacity .25s .25s,transform .25s .25s,background-color .25s}@media (min-width:900px){.nav--entry--enter{top:.375em}}.nav--entry--enter:hover{background-color:#dd5cb3}.nav--entry--soon{background-color:#f8f8f8;border-radius:.5em;display:block;font-size:.8333333333rem;padding:0 .75em;position:absolute;right:.75em;top:.375em;transform:translateY(.45em)}.nav--entry--description{font-size:.8333333333rem;line-height:1.2;margin-top:.75em;opacity:0;transform:translateY(.35em);transition:all .25s}.nav--entry:before{background-color:#fff;-o-border-image:linear-gradient(#e7abfa,#4f67f3,#de5bb5,#edb772,#dd5cb3) 30;border-image:linear-gradient(#e7abfa,#4f67f3,#de5bb5,#edb772,#dd5cb3) 30;border-left:2px solid;border-right:2px solid;bottom:0;box-shadow:0 .75em 1.2em -.125em rgba(34,34,34,.5),0 0 0 1px rgba(34,34,34,.02);content:\"\";left:-.75em;opacity:0;position:absolute;right:-1.5em;top:-.75em;transition:all .25s;z-index:-1}@media (min-width:900px){.nav--entry:before{left:-.75em;right:-2.25em}}.nav--entry.selected{transform:scale(1.05) translateY(0);z-index:99}.nav--entry.selected:before{opacity:1}.nav--entry.selected .nav--entry--button,.nav--entry.selected .nav--entry--enter,.nav--entry.selected p{opacity:1;transform:translateY(0)}", "",{"version":3,"sources":["webpack://./src/assets/styles/_fonts.scss","webpack://./src/assets/scripts/components/Menu.vue","webpack://./src/assets/styles/_transitions.scss","webpack://./src/assets/styles/base.scss","webpack://./src/assets/styles/_variables.scss"],"names":[],"mappings":"AAAA,WAMI,kBAAA,CALA,kBAAA,CAIA,iBAAA,CADA,eAAA,CAFA,mGCIJ,CCNA,sCACE,qBDQF,CCNA,2BACE,SDSF,CCNA,wCACE,yBAAA,CACA,kCDSF,CCNA,oBAEE,oBAAA,CADA,mCDUF,CCNA,6BACE,0BAAA,CACA,cDSF,CCNA,6BACE,uBAAA,CACA,cDSF,CCLA,wCACE,wBDQF,CCLA,oBACE,sBDQF,CCDA,6BACE,0BDQF,CCHA,+B